<template>
    <div class="newsdetail-layout"  v-cloak v-show="isNewsData">
        <header class="data-content-topbar clearfix" v-show="!isWeiXinBowser">
            <a class="data-content-back" onclick="window.history.go(-1);">
            </a>
            <a  id="data-content-more" class="data-content-more" @click="toshare()"></a>
        </header>
        <main class="tiezi-detai-main" :class="{'tiezi-detai-main-bower':!isWeiXinBowser}">
            <section class="tiezi-detail-item1" :class="{'tiezi-detail-item1-bowser':!isWeiXinBowser,'tiezi-detail-item1-normal':isWeiXinBowser}">
                <img v-lazy="newDetailObj.avatar" alt="" class="tiezi-detail-pic">
                <div class="tiezi-detail-infos">
                    <p class="tiezi-detail-name">
                        {{newDetailObj.author}}
                    </p>
                    <div class="tiezi-detail-date">
                    <span class="tiezi-detail-time">
                        {{newDetailObj.created_at}}
                    </span>
                      <span class="tiezi-detail-tag" v-show="tags.length>0">
                       <!-- <span v-for="(tag,index) in tags">{{tag.name}}<span v-show="(index+1)!==tags.length"></span></span>-->
                        {{tagName}}
                      </span>
                        <a @click="shouC($event)" class="tiezi-detail-shouc" >
                            
                        </a>
                    </div>

                </div>
            </section>
            <section class="tiezi-detail-item2">
                <h2 class="tiezi-article-title">
                    {{newDetailObj.title}}
                </h2>
                <div class="tiezi-item-contents">
                    <p class="tiezi-item-content" v-html="newDetailObj.body" id="editor">
                       {{newDetailObj.body}}
                    </p>
                    <!--<p class="tiezi-item-content">
                        
                    </p>-->
                </div>
            </section>
            <section class="tiezi-detail-item3 clearfix">
                <div class="tiezi-detail-allcom">
                <span class="tiezi-detail-num">
                    {{newDetailObj.article_comments_count}}
                </span>
                </div>
                <div style="font-size: 16px;width: 100%;text-align: center;margin-top: 10px;color: #3e3e3e" v-show="isShowText">!</div>
                <ul class="ul-detail">
                  <li class="tiezi-pinglun-item" v-for="(cItem,index) in articlesComments"  v-show="cItem.status!==0"  @click="showResDialog($event,cItem.id,cItem.commentator.id,cItem.commentator.personal_info.name,cItem.content,cItem.status,index)">
                    <img :src="cItem.commentator.personal_info.image.medium" alt="" class="tiezi-pinglun-pic">
                    <div class="tiezi-pinglun-infos">
                      <p class="tiezi-pinglun-name">{{cItem.commentator.personal_info.name}}</p>
                      <p class="tiezi-pinglun-time">{{cItem.created_at}}</p>
                      <p class="tiezi-pinglun-huifu">{{cItem.content}}</p>
                    </div>
                    <ul class="inner-ul">
                      <li class="tiezi-leave-msg-item tiezi-reply-msg-item" v-for="replyItem in replyComment" v-if="cItem.id==replyItem.comment_id">
                        <div class="tiezi-leave-msg-r tiezi-reply-msg-r"  @click="showReplyToReply($event,replyItem.comment_id,replyItem.from_uid.id,replyItem.from_uid.id,replyItem.from_uid.personal_info.name,cItem.commentator.id,1,replyItem.content)" >
                          <p class="tiezi-leave-msg-uname tiezi-reply-msg-uname">{{replyItem.from_uid.personal_info.name}}{{replyItem.to_uid.personal_info.name}}</p>
                          <p class="tiezi-leave-msg-content">{{replyItem.content}}</p>
                          <p class="tiezi-leave-msg-time">{{replyItem.replyed_at}}</p>
                        </div>
                      </li>
                    </ul>
                  </li>
                </ul>
            </section>
            <div class="my-line">

            </div>
            <footer class="tiezi-detail-item4 clearfix">
                <div class="l-input">
                    <a class="input-content" @click="goComentPage($event)">
                        <span class="com-text"></span>
                    </a>
                </div>
                <div class="tiezi-read">
                    <i class="read-icon"></i>
                    <span class="tiezide-read-num">
                        {{newDetailObj.article_views_count}}
                    </span>
                </div>
                <div class="r-tags clearfix">
                    <div class="r-zan-box" @click="putZan(newDetailObj.can_like)">
                      <i class="plus">+1</i>
                        <img src="../../../src/assets/images/r-zan.png" alt="" class="r-zan-pic">
                        <span id="likeNum">{{newDetailObj.article_likes_count}}</span>
                    </div>
                    <div class="r-fuhui-box">
                        <img src="../../../src/assets/images/r-huifu.png" alt="" class="r-fuhui-pic">
                        <span>{{newDetailObj.article_comments_count}}</span>
                    </div>
                </div>
            </footer>

        </main>
     <!-- <div v-show="isLoadingDone" class="am-share" style="display: none">
        <h3 class="am-share-title"></h3>
        <ul class="am-share-sns">
          <li>
            <a href="#">
              <img class="share-icon-weibo" src="../../../src/assets/images/firend_circle.png">
              <span></span>
            </a>
          </li>
          <li>
            <a href="#">
              <img class="share-icon-weibo" src="../../../src/assets/images/firend_circle.png">
              <span></span>
            </a>
          </li>
          <li>
            <a href="#">
              <img class="share-icon-weibo" src="../../../src/assets/images/send_friend.png">
              <span></span>
            </a>
          </li>
          <li>
            <a href="#">
              <img class="share-icon-weibo" src="../../../src/assets/images/firend_circle.png">
              <span></span>
            </a>
          </li>
        </ul>
        <div class="am-share-footer">
          <button class="share_btn"></button>
        </div>
      </div>-->
      <div  class="news-share" v-show="isLoadingDone">
        <button class="news-share-res" @click="replyNews()"></button>
        <button class="news-share-jubao" @click="goJuBaoPage()" v-show="isJuBao"></button>
        <button class="news-share-delete" @click="deleteReply()" v-show="isShowDelete"></button>
        <div class="news-black-line">

        </div>
        <div class="news-share-footer">
          <button class="news-share_btn"></button>
        </div>
      </div>
    </div>
</template>
<script>
    import Api from '../../api/api';
    import {getSignInfo} from '../../api/tiezidetail';
    import {dealColl} from '../../api/tiezidetail';
    import {delColl} from '../../api/tiezidetail';
    import {dealLike} from '../../api/tiezidetail';
    import {cancLike} from '../../api/tiezidetail';
    import {getViewsInfo} from '../../api/tiezidetail';
    import { MessageBox } from 'mint-ui';
    import logUtil from '../../utils/log';
    import wx from 'weixin-js-sdk';
    export default{
        data(){
        return {
          isWeiXinBowser:false,
          isShowDelete:false,
          //
          collectioned:-1,
          collection_or_cancel:-1,
          //
          liked:-1,
          buttonBottom: 0,
          isShowDialog:true,
          articleId:-1,
          collectionId:-1,
          fromId:'',
          isLoadingDone:false,
          isJuBao:false,
          isShowText:true,
          newDetailObj:{

          },
          desc:"",
          status:-1,
          //
          whoPingLun:'',
          //
          whoPingLunContent:'',
          //
          articlesComments:[],
          //
          replyComment:[],
          //
          tags:[],
          //id
          commentId:'',
          //id
          toId:'',
          commentator_id:'',
          //
          fromName:'',
          index:-1,
          isNewsData:false,

          //
          timestamp:"",
          noncestr:"",
          signature:"",
          appId:"",
          tagName:"",
        }
      },
        mounted() {
          /**
           * 
           */
         /* $('.am-share').bind("touchmove",function(e){
            e.preventDefault();
          });*/
//          this.checkBowser();
          /**
           * 
           */
          $('.news-share').bind("touchmove",function(e){
            e.preventDefault();
          });
          this.articleId = this.$route.params.tiezidetailId;
          this.isWeiXinBowser = this.isWeiXin();
          this.preventPageScroll();
          /**
           * 
           * */
          const _this = this;
           if(this.isWeiXin()){
            _this.getAsign();
          }else{
            this.transViews();
            this.getArticleById(this.$route.params.tiezidetailId);

          }
         /* if(this.isWeiXin()){
            wx.ready(function () {
              _this.transViews();
              _this.getArticleById(_this.$route.params.tiezidetailId);
            });
          }else{
            this.transViews();
            _this.getArticleById(_this.$route.params.tiezidetailId);
          }*/
        },
        methods:{
          getAsign(){
            const _this = this;
            //url()
            let url = encodeURIComponent(window.location.href.split('#')[0]);
            //
            getSignInfo(Api.SIGN,url).then((res)=>{
              _this.initAsign(res);
            },(err)=>{
            });
          },
          preventPageScroll(){
            var imgBox = document.getElementById("editor");
            var moveX = 0;
            var endX = 0;
            var startX = 0;
            /**
             * 
             */
            imgBox.addEventListener("touchstart", function (e) {
              var event = e || window.event;
              //
              startX = event.touches[0].clientX;
            }, false);
            imgBox.addEventListener("touchmove", function (e) {
              var event = e || window.event;
              endX = event.touches[0].clientX;
              //
              moveX = startX - endX;
              if (Math.abs(moveX) > 10) {
                event.preventDefault();
              }
//              console.log("moveX",Math.abs(moveX));
            }, false);
          },
          /**
           * 
           * */
          isWeiXin(){
            //
            let ua = window.navigator.userAgent.toLowerCase();
            if (ua.match(/MicroMessenger/i) == 'micromessenger') {
              return true;
            } else {
              return false;
            }
          },
          dealCollect(id){
            const _this = this;
            dealColl(Api.COLLECTIONS,id).then((res)=>{
              if(res.status === 200 && res.data.data.detail!==null){
                _this.collection_or_cancel = 1;
                $('.tiezi-detail-shouc').html('');
                _this.showMsg('!');
              }
            },(err)=>{
              _this.showMsg('!');
              _this.$store.dispatch('hideLoading');
            });
          },
          cancleCollect(id){
             const _this = this;
            //
            delColl(Api.COLLECTIONS,id).then((res)=>{
              if(res.status === 204){
                _this.collection_or_cancel = 0;
                $('.tiezi-detail-shouc').html('+');
                _this.showMsg('!');
              }
            },(err)=>{
              _this.showMsg('!');
              _this.$store.dispatch('hideLoading');
            });
          },
          shouC($event){
            let token = localStorage.getItem('token');
            const _this = this;
            if(token){
              if(this.$route.params.tiezidetailId!==null || this.$route.params.tiezidetailId!=='' || this.$route.params.tiezidetailId!=='undefine'){
                switch(_this.collection_or_cancel){
                  case 0: //,
                      _this.dealCollect(this.$route.params.tiezidetailId);
                      break;
                  case 1: //,
                      _this.cancleCollect(this.$route.params.tiezidetailId);
                      break;
                }
              }
            }else{
              //
              this.$router.push({path: '/login', replace: true});
            }
            $event.preventDefault();
          },
          dealLike(newsId){
           const _this = this;
            dealLike(Api.NEWSZAN,newsId).then((res)=>{
              if(res.status === 200){
                _this.liked = 1;
                let topele=$("#likeNum"),topcurrent = parseInt(topele.text());
                $("#likeNum").css({
                  'color':'#20A0FF'
                });
                $(".plus").show(0).stop(true).animate({"top":-18,opacity:0},500,function(){topele.text(topcurrent + 1);});
                _this.showMsg('!');
//                    _this.$router.go(0);
              }
            },(err)=>{
              _this.$store.dispatch('hideLoading');
            });
          },
          cancleLike(newsId){
             const _this = this;
            cancLike(Api.NEWSZAN,newsId).then((res)=>{
              if(res.status === 204){
                _this.liked = 0;
                let topele=$("#likeNum"),topcurrent = parseInt(topele.text());
                $(".plus").show(0).stop(true).animate({"top":-18,opacity:0},500,function(){topele.text(topcurrent - 1);});
                $("#likeNum").css({
                  'color':'#9f9f9f'
                });
                _this.showMsg('!');
              }else{
                _this.showMsg('!');
                _this.$store.dispatch('hideLoading');
              }
            },(err)=>{
              _this.$store.dispatch('hideLoading');
            });
          },
          /**
           * 
           * */
          putZan(canLike){
              const _this = this;
              let token = localStorage.getItem('token');
              if(token===null || token===''){
                this.$router.push({path: '/login', replace: true});
              }
              if(canLike === 0){

                this.showMsg(',');
                return;
              }
              let newsId = this.$route.params.tiezidetailId;
              if(newsId!==null && $.isNumeric(newsId)) {
                switch (this.liked){
                  case 0://,
                      _this.dealLike(newsId);
                      break;
                  case 1://
                      _this.cancleLike(newsId);
                      break;
                }
              }
          },
          showMsg(str){
            this.$toast({
              message: str,
              duration: 2000,
            });
          },
          /**
           * 
           * */
          showResDialog($event,commentId,commentator_id,commentName,commentContent,status,index){
            const _this = this;
//            alert("");
            $(".news-share").addClass("news-modal-active");
            _this.showMask();
            $(".news-sharebg-active,.news-share_btn").click(function () {
              _this.hideMask();
            });
            _this.commentId = commentId;
            _this.commentator_id = commentator_id;
            _this.status = status;
            _this.index = index;
//            console.log("",_this.commentator_id,localStorage.getItem('userId'),index);
            if(_this.commentator_id === parseInt(localStorage.getItem('userId'))){
              _this.isShowDelete = true;
              _this.isJuBao = false;
            }else{
              _this.isJuBao = true;
              _this.isShowDelete = false;
            }
            _this.whoPingLun = commentName;
            _this.whoPingLunContent = commentContent;
            $event.stopPropagation();
            window.event.cancelBubble = true;
          },
          /**
           * 
           * */
          showMask(){
            if ($(".news-sharebg").length > 0) {
              $(".news-sharebg").addClass("news-sharebg-active");
            } else {
              $(".newsdetail-layout").append('<div class="news-sharebg"></div>');
              $(".news-sharebg").addClass("news-sharebg-active");
            }
          },
          /**
           * 
           * */
          hideMask(){
            $(".news-share").removeClass("news-modal-active");
            setTimeout(function () {
              $(".news-sharebg-active").removeClass("news-sharebg-active");
              $(".news-sharebg").remove();
            }, 300);
          },
          /**
           * 
           **/
          transViews(){
            let newsId = this.$route.params.tiezidetailId;
            if(newsId!==null && $.isNumeric(newsId)){
              getViewsInfo(Api.NEWSVIEW,newsId).then((res)=>{

              },(err)=>{

              });
            }

          },
          /**
           * 
           * @param id
           */
          /**
           * 
           * */
          getCommentsInfo(id){
            return this.$http.get(Api.NEWSCOMMENTINFOS+`/${id}/comments/`);
          },
          /**
           * 
           * */
            getNewsDetailInfo(id){
              return this.$http.get(Api.ARTICLESDETAIL+`${id}/`);
            },

          /**
           * 
           * @param id
           */
            getArticleById(id){
            const that = this;
            this.$http.
            all([that.getNewsDetailInfo(id),that.getCommentsInfo(id)])
              .then(this.$http.spread(function (acct,perms){
                //acct,perms
                that.initNewsInfo(acct);
                that.initCommentsInfo(perms);
              }),(err)=>{
                that.showMsg('');
                that.$store.dispatch('hideLoading');
              });
          },
            initNewsInfo(acct){
              logUtil.printLog(":",acct);
              const _this = this;
              if(acct.status === 200 && acct.data!==null){
                this.newDetailObj = acct.data.data.detail;
                var str = window.location.href;
                var regEn1 = /[?]/im;
                if(regEn1.test(str) ) {
                  var arr = str.split("?")[0];
                  var arr1 = str.split("?")[1].split("#")[1];
                  str = arr+"#"+arr1;
                }
                //
                wx.onMenuShareTimeline({
                  title: _this.newDetailObj.title, // 
                  link:str, // JS
                  imgUrl: _this.newDetailObj.image, // 
                  success: function (res) {
                    // 
                    logUtil.printLog(":",res);
                    _this.showMsg("!")
                  },
                  cancel: function (res) {
                    // 
                    logUtil.printLog(":",res);
                  }
                });
                //
                wx.onMenuShareAppMessage({
                  title: _this.newDetailObj.title, // 
                  desc: _this.desc, // 
                  link: str, // JS
                  imgUrl: _this.newDetailObj.image, // 
                  type: '', // ,musicvideolinklink
                  dataUrl: '', // typemusicvideo
                  success: function (res) {
                    // 
                    logUtil.printLog(":",res);
                  },
                  cancel: function (res) {
                    // 
                    logUtil.printLog(":",res);
                  }
                });
                //QQ
                wx.onMenuShareQQ({
                  title: _this.newDetailObj.title, // 
                  desc: _this.desc, // 
                  link: str, // 
                  imgUrl: _this.newDetailObj.image, // 
                  success: function (res) {
                    // 
                    logUtil.printLog("QQ:",res);
                  },
                  cancel: function (res) {
                    // 
                    logUtil.printLog("QQ:",res);
                  }
                });

                //QQ
                wx.onMenuShareQZone({
                  title: _this.newDetailObj.title, // 
                  desc: _this.desc, // 
                  link: str, // 
                  imgUrl: _this.newDetailObj.image, // 
                  success: function (res) {
                    // 
                    logUtil.printLog("QQ:",res);
                  },
                  cancel: function (res) {
                    // 
                    logUtil.printLog("QQ:",res);
                  }
                });
                let temBody = this.newDetailObj.body.replace(/<\/?.+?>/g,"");
                this.desc = temBody.replace(/ /g,"");;
                logUtil.printLog(":",this.desc);
                //:1,2,0
                this.collectioned = this.newDetailObj.collectioned;
                this.liked =  this.newDetailObj.liked;
                this.tags =  this.newDetailObj.tags;
                this.tagName = "";
                let len = this.tags.length;
                for(let i=0;i<len;i++){
                  if(i<=2){
                    if(i+1===len){
                      if(i>=2){
                        this.tagName+=this.tags[i].name+"......";
                      }else{
                        this.tagName+=this.tags[i].name;
                      }

                    }else {
                      this.tagName+=this.tags[i].name+"";
                    }
                  }
                }
                this.collection_or_cancel =  this.newDetailObj.collection_or_cancel;
                if(this.collection_or_cancel ===1){
                  $('.tiezi-detail-shouc').html('');
                }else{
                  $('.tiezi-detail-shouc').html('+');
                }
                this.isNewsData = true;
              }else{
                this.isNewsData = false;
                this.showMsg('');
                this.$store.dispatch('hideLoading');
              }
          },
            initCommentsInfo(perms){
                logUtil.printLog(":",perms);
                if(perms.status ===200 && perms.data.data.detail!==null){
                  this.articlesComments = perms.data.data.detail;
                  let len = this.articlesComments.length;
                  this.replyComment = [];
                  if(len>0){
                    this.isLoadingDone = true;
                  }else{
                    this.isLoadingDone = false;
                  }
                  len>=1?this.isShowText = false:this.isShowText = true;
                  for(let j=0;j<len;j++){
                    let kLen = this.articlesComments[j].reply_comment.length;
                    for(let k = 0;k<kLen;k++){
                      this.replyComment.push(this.articlesComments[j].reply_comment[k]);
                    }
                  }
                  logUtil.printLog(":",len);
                }else{

                }
          },
            goComentPage($event){
                this.$router.push({path: `/writecoment/${this.articleId}`, replace: true});
                $event.preventDefault();
            },
          /**
           * 
           * */
          goJuBaoPage(){
            this.$router.push({path: `/jubao/${this.commentId}/${this.whoPingLun}/${this.whoPingLunContent}`});
          },
    /**
           * ()
           * */
          deleteReply(){
            this.hideMask();
            const _this = this;
            MessageBox({
              title: '',
              message: '?',
              showCancelButton: true
            });
            MessageBox.confirm('?').then(action => {
              let token = localStorage.getItem('token');
              const _this = this;
              logUtil.printLog(":",_this.index);
              if(token){
                  if(_this.status!==-1||_this.status!==0){
                    _this.$http.put(`${Api.COMMENTNEWS}`,{
                      id:_this.commentId,
                      status:0
                    }).then((res)=>{
                      logUtil.printLog("infos:",res);
                      if(res.status === 200){
                        _this.showMsg('!');
                        logUtil.printLog(":",this.articlesComments.length);
                      _this.$router.go(0);
                      }
                    },(err)=>{
                      _this.showMsg('!');
                      _this.$store.dispatch('hideLoading');
                    })
                  }

              }else{
                this.$router.push({path: '/login', replace: true});
              }
            });
          },
          /**
           * 
           */
          showReplyToReply($event,commentId,fromId,toId,fromName,commentator_id,flag,content){
            const _this = this;
            $(".news-share").addClass("news-modal-active");
            _this.showMask();
            $(".news-sharebg-active,.news-share_btn").click(function () {
              _this.hideMask();
            });
            this.commentId = commentId;
            this.fromId = fromId;
            this.toId = toId;
            _this.commentator_id = commentator_id;
            _this.whoPingLun = fromName;
            _this.whoPingLunContent = content;
//            console.log(":",$(this).parent('.inner-ul'),flag);
            if(_this.commentator_id === parseInt(localStorage.getItem('userId'))){

              _this.isShowDelete = true;
              _this.isJuBao = false;
            }else{
              _this.isJuBao = true;
              _this.isShowDelete = false;
            }
            this.fromName = fromName;
            if(flag === 1){
              $event.stopPropagation();
              window.event.cancelBubble = true;
            }
//            $event.stopPropagation();
//            window.event.cancelBubble = true;
          },
          /**
           *
           **/
          replyNews(){
            this.hideMask();
            if(this.fromId === ''|| this.fromId===null){
              this.$router.push({path: `/replymsg/${this.$route.params.tiezidetailId}/${this.commentId}/${this.commentator_id}`});
            }else{
              this.$router.push({path: `/replytoreply/${this.commentId}/${this.fromId}/${this.toId}/${this.fromName}`});
            }

          },
          initAsign(res){
            const _this = this;
            if(res.status === 200){
              //,
              let sinObj = res.data.data.detail;
              wx.config({
                debug: false,
                appId: sinObj.appId, // Ticke------
                timestamp:sinObj.timestamp.toString(), // 
                nonceStr: sinObj.nonceStr, // 
                signature: sinObj.signature,// 1
                //:QQQQ
                jsApiList: [
                  'onMenuShareAppMessage','onMenuShareTimeline',
                  'onMenuShareQQ','onMenuShareQZone'
                ]
              });
              wx.ready(function () {
                _this.transViews();
                _this.getArticleById(_this.$route.params.tiezidetailId);
              });
            }
          },
        },

      /**
       * 
       * */

      watch:{
        $route(to){
          //id()
          let reg=/tiezidetail\/\d+/;
          const _this = this;
          if(reg.test(to.path)){
            this.hideMask();
            let newsId=_this.$route.params.tiezidetailId || 0;
            if(this.isWeiXin()){
              _this.getAsign();
            }else{
              _this.transViews();
              _this.getArticleById(_this.$route.params.tiezidetailId);

            }
            /*if(this.isWeiXin()){
              wx.ready(function () {
                _this.transViews();
                _this.getArticleById(_this.$route.params.tiezidetailId);
              });
            }else{
              _this.transViews();
              _this.getArticleById(_this.$route.params.tiezidetailId);
            }*/
          }
        }
      },
    }
</script>
<style scoped>
    @import "../../assets/css/ttiezidetail.css";
    [v-cloak] {
      display: none !important;
    }
    body {
        background-color: #ffffff;
    }

</style>



